import styled from "styled-components"
import "bootstrap/dist/css/bootstrap.min.css"
import MainBanner from "../../shared-components/MainBanner/MainBanner"
import CardChapitre from "../../shared-components/CardChapitre/CardChapitre"
import { Icone } from "../../Data/Icone"

const BlockMatter = styled.div`
  border: solid #e5e5e5;
  border-radius: 12px;
  border-width: 2px 2px 4px;
  cursor: pointer;
  outline: 0;
  touch-action: manipulation;
  transform: translateZ(0);
  padding: 50px 10px 100px 10px;
`

const Matter = () => {
  return (
    <>
      <MainBanner />

      <div
        className="text-center"
        style={{ paddingBottom: 50, paddingTop: 50 }}
      >
        <h4>
          <img src={Icone.maths} height="40" />
          Mathématique
        </h4>
      </div>

      <div className="container mb-3">
        <BlockMatter
          className="row d-flex justify-content-around mt-5 "
          style={{ marginBottom: 30, marginTop: 50 }}
        >
          <CardChapitre name_lesson={"Inequation et inéquation"} />
          <CardChapitre name_lesson={"Trigonometrie"} />
          <CardChapitre name_lesson={"Fonction numérique"} />
          <CardChapitre name_lesson={"Limites et continuité"} />
          <CardChapitre name_lesson={"Dérivation"} />
          <CardChapitre name_lesson={"Suites numérique"} />
          <CardChapitre name_lesson={"Arithmétique"} />
          <CardChapitre name_lesson={"Statistique"} />
          <CardChapitre name_lesson={"Dénombrement"} />
        </BlockMatter>
      </div>
    </>
  )
}

export default Matter
